var __self
export default {
	data(){
		return{
		mode: 'aspectFit',
		imgpath:"",
		respongs:[],
		autoplay:true,
		interval:10000,
		duration:1000,
		}
	},
	onLoad:function(e){
		__self=this;
		// console.log(e);
		var data = JSON.parse(decodeURIComponent(e.categoryid));
		uni.request({
			url:'http://service.picasso.adesk.com/v1/vertical/category/'+data+'/vertical',
			method:"GET",
			success:res => {
			
				__self.respongs=res.data.res.vertical;
				
			},
			fail: () => {
				console.log("this is fail");
			},
		});
		
		},
	methods: {
			
		}
	}

